<template>
    <div class="shape" :class="{ active: this.active }" @click="selectCurComponent" @mousedown="handleMouseDownOnShape">
        <i class="el-icon-refresh-right" v-show="active" @mousedown="handleRotate"></i>
        <div
            class="shape-point"
            v-for="(item, index) in (active? pointList : [])"
            @mousedown="handleMouseDownOnPoint(item, $event)"
            :key="index"
            :style="getPointStyle(item)">
        </div>
        <slot></slot>
    </div>
</template>

<script>
import eventBus from '@/utils/eventBus'
import runAnimation from '@/utils/runAnimation'
import { mapState } from 'vuex'
import calculateComponentPositonAndSize from '@/utils/calculateComponentPositonAndSize'

export default {
    props: {
        active: {
            type: Boolean,
            default: false,
        },
        element: {
            require: true,
            type: Object,
        },
        defaultStyle: {
            require: true,
            type: Object,
        },
        index: {
            require: true,
            type: [Number, String],
        },
    },
    data() {
        return {
            pointList: ['lt', 't', 'rt', 'r', 'rb', 'b', 'lb', 'l'], // 
            initialAngle: { // 
                lt: 0,
                t: 45,
                rt: 90,
                r: 135,
                rb: 180,
                b: 225,
                lb: 270,
                l: 315,
            },
            angleToCursor: [ // 
                { start: 338, end: 23, cursor: 'nw' },
                { start: 23, end: 68, cursor: 'n' },
                { start: 68, end: 113, cursor: 'ne' },
                { start: 113, end: 158, cursor: 'e' },
                { start: 158, end: 203, cursor: 'se' },
                { start: 203, end: 248, cursor: 's' },
                { start: 248, end: 293, cursor: 'sw' },
                { start: 293, end: 338, cursor: 'w' },
            ],
            cursors: {},
        }
    },
    computed: mapState([
        'curComponent',
    ]),
    mounted() {
        eventBus.$on('runAnimation', () => {
            if (this.element == this.curComponent) {
                runAnimation(this.$el, this.curComponent.animations)
            }
        })
    },
    methods: {
        // 
        handleRotate(e) {
            e.stopPropagation()
            // 
            const pos = { ...this.defaultStyle }
            const startY = e.clientY
            const startX = e.clientX
            const startRotate = pos.rotate

            // 
            const rect = this.$el.getBoundingClientRect()
            const centerX = rect.left + rect.width / 2
            const centerY = rect.top + rect.height / 2

            // 
            const rotateDegreeBefore = Math.atan2(startY - centerY, startX - centerX) / (Math.PI / 180)

            // 
            let hasMove = false
            const move = (moveEvent) => {
                hasMove = true
                const curX = moveEvent.clientX
                const curY = moveEvent.clientY
                // 
                const rotateDegreeAfter = Math.atan2(curY - centerY, curX - centerX) / (Math.PI / 180)
                // 
                pos.rotate = startRotate + rotateDegreeAfter - rotateDegreeBefore
                // 
                this.$store.commit('setShapeStyle', pos)
            }

            const up = () => {
                hasMove && this.$store.commit('recordSnapshot')
                document.removeEventListener('mousemove', move)
                document.removeEventListener('mouseup', up)
                this.cursors = this.getCursor() // 
            }

            document.addEventListener('mousemove', move)
            document.addEventListener('mouseup', up)
        },

        getPointStyle(point) {
            const { width, height } = this.defaultStyle
            const hasT = /t/.test(point)
            const hasB = /b/.test(point)
            const hasL = /l/.test(point)
            const hasR = /r/.test(point)
            let newLeft = 0
            let newTop = 0
            
            // 
            if (point.length === 2) {
                newLeft = hasL? 0 : width
                newTop = hasT? 0 : height
            } else {
                // 
                if (hasT || hasB) {
                    newLeft = width / 2
                    newTop = hasT? 0 : height
                }

                // 
                if (hasL || hasR) {
                    newLeft = hasL? 0 : width
                    newTop = Math.floor(height / 2)
                }
            }
            
            const style = {
                marginLeft: hasR? '-4px' : '-4px',
                marginTop: '-4px',
                left: `${newLeft}px`,
                top: `${newTop}px`,
                cursor: this.cursors[point],
            }
            
            return style
        },

        getCursor() {
            const { angleToCursor, initialAngle, pointList, curComponent } = this
            const rotate = (curComponent.style.rotate + 360) % 360 //  + 360
            const result = {}
            let lastMatchIndex = -1 // 
            pointList.forEach(point => {
                const angle = (initialAngle[point] + rotate) % 360
                const len = angleToCursor.length
                while (true) {
                    lastMatchIndex = (lastMatchIndex + 1) % len
                    const angleLimit = angleToCursor[lastMatchIndex]
                    if (angle < 23 || angle >= 338) {
                        result[point] = 'nw-resize'
                        return
                    }

                    if (angleLimit.start <= angle && angle < angleLimit.end) {
                        result[point] = angleLimit.cursor + '-resize'
                        return
                    }
                }
            })

            return result
        },

        handleMouseDownOnShape(e) {
            if (this.element.component != 'v-text') {
                e.preventDefault()
            }

            e.stopPropagation()
            this.$store.commit('setCurComponent', { component: this.element, index: this.index })
            this.cursors = this.getCursor() // 

            const pos = { ...this.defaultStyle }
            const startY = e.clientY
            const startX = e.clientX
            // 
            const startTop = Number(pos.top)
            const startLeft = Number(pos.left)

            // 
            let hasMove = false
            const move = (moveEvent) => {
                hasMove = true
                const curX = moveEvent.clientX
                const curY = moveEvent.clientY
                pos.top = curY - startY + startTop
                pos.left = curX - startX + startLeft

                // 
                this.$store.commit('setShapeStyle', pos)
                // 
                //  $nextTick
                this.$nextTick(() => {
                    // 
                    // 
                    // curY - startY > 0 true  false 
                    // curX - startX > 0 true  false 
                    eventBus.$emit('move', curY - startY > 0, curX - startX > 0)
                })
            }

            const up = () => {
                hasMove && this.$store.commit('recordSnapshot')
                // 
                eventBus.$emit('unmove')
                document.removeEventListener('mousemove', move)
                document.removeEventListener('mouseup', up)
            }

            document.addEventListener('mousemove', move)
            document.addEventListener('mouseup', up)
        },

        selectCurComponent(e) {
            // 
            e.stopPropagation()
            e.preventDefault()
            this.$store.commit('hideContexeMenu')
        },

        handleMouseDownOnPoint(point, e) {
            const downEvent = window.event
            downEvent.stopPropagation()
            downEvent.preventDefault()
 
            const style = { ...this.defaultStyle }
            const center = {
                x: style.left + style.width / 2,
                y: style.top + style.height / 2,
            }

            // 
            const editorRectInfo = document.querySelector('#editor').getBoundingClientRect()

            // 
            const curPoint = {
                x: e.clientX - editorRectInfo.left,
                y: e.clientY - editorRectInfo.top,
            }

            // 
            const symmetricPoint = {
                x: center.x - (curPoint.x - center.x),
                y: center.y - (curPoint.y - center.y),
            }

            // 
            let needSave = false
            let isFirst = true
            const move = (moveEvent) => {
                //  move
                //  move 
                if (isFirst) {
                    isFirst = false
                    return
                }

                needSave = true
                const curPositon = {
                    x: moveEvent.clientX - editorRectInfo.left,
                    y: moveEvent.clientY - editorRectInfo.top,
                }
                
                calculateComponentPositonAndSize(point, style, curPositon, {
                    center,
                    curPoint,
                    symmetricPoint,
                })

                this.$store.commit('setShapeStyle', style)
            }

            const up = () => {
                document.removeEventListener('mousemove', move)
                document.removeEventListener('mouseup', up)
                needSave && this.$store.commit('recordSnapshot')
            }

            document.addEventListener('mousemove', move)
            document.addEventListener('mouseup', up)
        },
    },
}
</script>

<style lang="scss" scoped>
.shape {
    position: absolute;

    &:hover {
        cursor: move;
    }
}
.active {
    outline: 1px solid #70c0ff;
    user-select: none;
}
.shape-point {
    position: absolute;
    background: #fff;
    border: 1px solid #59c7f9;
    width: 8px;
    height: 8px;
    border-radius: 50%;
}
.el-icon-refresh-right {
    position: absolute;
    top: -30px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 18px;
    font-weight: 600;
    cursor: grab;
    color: #59c7f9;
    font-size: 22px;
    font-weight: normal;

    &:active {
        cursor: grabbing;
    }
}
</style>